import { useEffect, useState } from "react"
import { get } from "../services/api"
import "../style/style.css"
export default function Search() {
    const [searchParams, setSearchParams] = useState({})
    const [searchData, setSeachData] = useState()
    const [showTab, setShowTab] = useState(false);
    const debounce = (func, delay) => {
        let debounceTimer;
        return function () {
            const context = this;
            const args = arguments;
            clearTimeout(debounceTimer);
            debounceTimer = setTimeout(() => func.apply(context, args), delay);
        }
    }
    const handleChange = (e) => {
        setSearchParams({
            ...searchParams,
            [e.target.name]: e.target.value
        })
    }
    console.log(searchParams.search)
    const debouncedHandleChange = debounce(handleChange, 1000);

    const handleSubmit = e => {
        e.preventDefault();
        const url = `https://api.themoviedb.org/3/search/movie?query=${searchParams.search}&include_adult=true&language=en-US&page=1`
        get('get', url)
            .then(resp =>
                console.log(resp)
            )
        console.log(searchParams.search)
    }
    useEffect(() => {
        const url = `https://api.themoviedb.org/3/search/movie?query=${searchParams.search}&include_adult=true&language=en-US&page=1`
        get('get', url)
            .then(resp =>
                {console.log(resp)
                setSeachData(resp)}
            )
    }, [searchParams.search])
    const handleFocus = () => {
        setShowTab(true);
    };

    const handleBlur = () => {
        setTimeout(() => {
            setShowTab(false);
        }, 200);
    };
    console.log(showTab)
    return (
        <div className="search-contain">
            <form onSubmit={handleSubmit}>
                <input
                    id='searchBar'
                    className='searchBar'
                    onChange={debouncedHandleChange}
                    name='search'
                    onFocus={handleFocus}
                    onBlur={handleBlur}
                >
                </input>
                <button type="sunbmit" className="search-icon"><i className="fa-solid fa-magnifying-glass"></i></button>
            </form>
            <>
                <div className={`search-tab ${showTab ? 'show-search z-index-6' : 'hide-search z-index-6'}`}>
                   {searchData?searchData.results.slice(1,6).map((movie, index)=>
                    <div className="search-movie-item">
                        <div className="search-movie-title display-flex-center">{movie.title}</div>
                        <img className={`search-movie-img display-flex-center ${1 ? '' : ''}`} src={movie.backdrop_path ?
                        `https://image.tmdb.org/t/p/w500${movie.backdrop_path}` :
                        `https://image.tmdb.org/t/p/w500/qF0ZIAe1DRe49ql02KaYl04gnxt.jpg`}
                        alt='movie poster' />
                    </div>
                    ):
                    <div className="search-movie-item">Searching</div>}
                </div>
            </>
        </div>
    )
}