import React, { useEffect, useState } from 'react';
import { Link, NavLink, useNavigate, useParams } from 'react-router-dom';
import { Navigate } from 'react-router-dom';
import '../style/reset.css'
import '../style/style.css'
import Genres from './Genres';
import Aside from '../components/Aside';
import { get } from '../services/api';
import { useOutletContext } from 'react-router-dom';
import Carousel from '../components/Carousel';
function Home() {
  const { isScrolling } = useOutletContext();
  const [data, setData] = useState(null)
  const [allmovieData, setAllmovieData] = useState(null)
  const [page, setPage] = useState(1)
  const [pageNotUpdate, setPageNotUpdate] = useState(1)
  const navigate = useNavigate();
  const { movieListName } = useParams()
  let { pn } = useParams()
  let pageNumber = Number(pn);
  const movieList = ['changes', 'popular', 'top_rated', 'upcoming']

  useEffect(() => {
    window.scrollTo(0, 0);
  },[]);
  useEffect(() => {
    const url = 'https://api.themoviedb.org/3/genre/movie/list?language=en';
    get('get', url)
      .then(resp =>
        setData(resp)
      )
  }, []);
  useEffect(() => {
    let url;
    if (movieListName && movieList.includes(movieListName)) {
      url = `https://api.themoviedb.org/3/movie/${movieListName}?page=${pn}`;
    } else if (data && data.genres && movieList.includes(movieListName)) {
      let genreId = data.genres.find(obj => obj.name === movieListName).id;
      url = `https://api.themoviedb.org/3/discover/movie?include_adult=false&include_video=false&language=en-US&page=${pn}&sort_by=popularity.desc&with_genres=${genreId}`
    } else {
      url = `https://api.themoviedb.org/3/movie/now_playing?language=en-US&page=${page}`;
    }
    get('get', url)
      .then(resp => {
        setAllmovieData(resp)
      })
  }, [movieListName, pn]);
  const handleClick = (movie) => {
    console.log(movie.genre_ids[0])
    const selectedGenre = data.genres.find(genre => genre.id === Number(movie.genre_ids[0]));
    navigate(`${selectedGenre.name}/${movie.id}`)
  }
  const nextPage = () => {
    if (pn <= 100) {
      navigate(`/react-film-route/${movieListName}/page/${Number(pn) + 1}`)
    } else if (!pn) {
      setPage(prev => prev + 1)
      navigate(`/react-film-route/now_playing/page/2`)
    } else (pn = 1)

  }
  const prevPage = () => {
    if (pn < 100 && pn) {
      pn = -1
    } else if (pn) pn = 1
  }
  //Các nút để chọn trang
  let buttonArray = [];
  let totalPages;
  if (allmovieData && allmovieData.total_pages) {
    totalPages = allmovieData.total_pages;
    if (totalPages >= 9) {
      switch (Number(pn)) {
        case 1:
          buttonArray = [1, 2, 3, '...', totalPages];
          break;
        case 2:
          buttonArray = [1, 2, 3, 4, '...', totalPages];
          break;
        case 3:
          buttonArray = [1, 2, 3, 4, 5, '...', totalPages];
          break;
        case 4:
          buttonArray = [1, 2, 3, 4, 5, 6, '...', totalPages];
          break;
        case (totalPages - 1):
        case (totalPages - 2):
        case (totalPages - 3):
          buttonArray = [1, 2, '...', totalPages - 5, totalPages - 4, totalPages - 3, totalPages - 2, totalPages - 1, totalPages];
          break;
        case totalPages:
          buttonArray = [1, 2, '...', totalPages - 5, totalPages - 4, totalPages - 3, totalPages - 2, totalPages - 1, totalPages];
          break;
        // Add more cases as needed
        default:
          buttonArray = [1, 2, 3, 4, 5, 6, '...', totalPages];
          break;
      }
    } else buttonArray = Array.from({ length: totalPages }, (_, i) => i + 1);
  }
  const handlePageButton = (item) => {
    if (typeof (item) === 'number' && movieListName) {
      navigate(`/react-film-route/${movieListName}/page/${item}`)
    } else if (typeof (item) === 'number') { navigate(`/react-film-route/now_playing/page/${item}`) }
  }
  const handleSubmitPage = (e) => {
    e.preventDefault();
    navigate(`/react-film-route/${movieListName}/page/${pageNotUpdate}`)
    document.getElementById('pageInput').value = '';
  }
  const handleChange = e => {
    if (e.target.value <= totalPages) {
      setPageNotUpdate(e.target.value)
    }
  }
  //Check mouse over item or not
  const [mouseOverIndex, setMouseOverIndex] = useState(null);
  const mouseOverHandle = (index) => {
    setMouseOverIndex(index)
  }
  const mouseOutHandle = () => {
    setMouseOverIndex(-1);
  };
  return (
    <div className={`z-index-4 pageSetup home-bg position-relative`}>
      <div className={`z-index-0 ${isScrolling ? `top-11rem-height` : 'top-11rem-height'}`}></div>
      <Carousel/>
      <div className='container'>
        <div className='main-container home-contain'>
          <div className='main-title'>
            <h3>Now Playing Movies<div className='line'></div></h3>
            <div className='black-line'></div>
          </div>
          {allmovieData && allmovieData.results && movieListName != 'changes' ?
            <>
              <ul className='movie-grid'>{allmovieData.results.map((movie, index) =>
                <li className='movieItem' onClick={() => handleClick(movie)} key={`home-movie ${index}`}
                onMouseOver={() => mouseOverHandle(index)} onMouseOut={mouseOutHandle}>
                  <div className={`movie-title ${mouseOverIndex === index ? 'bigger2' : ''}`}>
                    <p>{movie.title}</p>
                  </div>
                  <div className='movie-bg'>
                    <img className={`movie-img ${mouseOverIndex === index ? 'bigger' : ''}`} src={`https://image.tmdb.org/t/p/w500${movie.backdrop_path}`} alt='movie poster' />
                  </div>
                </li>
              )}
              </ul>
            </> : <p>{JSON.stringify(allmovieData)}</p>
          }
          <div className='buttonPages'>
            <button className={1 === Number(pn) ? 'button hidden' : 'button'} onClick={prevPage}>{String('<')}</button>
            {buttonArray && buttonArray.map((item, index) => (
              <button key={`home-button ${index}`} className={`button ${item === Number(pn) ? 'active' : ''}`} onClick={() => handlePageButton(item)}>
                {item}
              </button>
            ))}
            <button className={totalPages === Number(pn) ? 'button hidden' : 'button'} onClick={nextPage}>{String('>')}</button>
          </div>
          <form onSubmit={handleSubmitPage}>
            <label htmlFor='pageInput'>Page</label>
            <input id='pageInput' className='pageInput' onChange={handleChange}></input>
            <input type='submit' className='button'></input>
          </form>
        </div>
        <div className='aside-container home-contain'>
          <Aside />
        </div>
      </div>

    </div>
  )
}

export default Home;
